import React from "react";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import "./PersonalProject.css";
import covid from "./pictures/covid.png";
import goggle from "./pictures/goggle.png";
import instagram from "./pictures/instagram.png";
import isitopen from "./pictures/isitopen.png";
import pitchbooker from "./pictures/pitchbooker.png";
import terygram from "./pictures/terygram.png";
import todolist from "./pictures/todolist.png";

export default class PersonalProject extends React.Component {
  render() {
    return (
      <div id="personal-project-div">
        <div>
          <p id="bio">Personal projects:</p>
        </div>

        <div className="personal-project-container">
          <Card style={{ width: "32rem" }}>
            <Card.Img variant="top" src={isitopen} />
            <Card.Body>
              <Card.Title>Is it open?</Card.Title>
              <Card.Text>Look up restaurant open timings</Card.Text>
              <Button
                variant="primary"
                href="https://is-itopen-frontend.herokuapp.com/"
              >
                Here
              </Button>
            </Card.Body>
          </Card>

          <Card style={{ width: "32rem" }}>
            <Card.Img variant="top" src={covid} />
            <Card.Body>
              <Card.Title>Covid-Visualizer</Card.Title>
              <Card.Text>
                See the daily growth of Covid-19 from any country.
              </Card.Text>
              <Button
                variant="primary"
                href="https://perrylzx.github.io/covid-visualizer/"
              >
                Here
              </Button>
            </Card.Body>
          </Card>
          <Card style={{ width: "32rem" }}>
            <Card.Img variant="top" src={pitchbooker} />
            <Card.Body>
              <Card.Title>pitch-booker</Card.Title>
              <Card.Text>A pitch booker app.</Card.Text>
              <Button
                variant="primary"
                href="https://pitch-booker.herokuapp.com"
              >
                Here
              </Button>
            </Card.Body>
          </Card>
          <Card style={{ width: "32rem" }}>
            <Card.Img variant="top" src={instagram} />
            <Card.Body>
              <Card.Title>Instagram</Card.Title>
              <Card.Text>An image sharing app.</Card.Text>
              <Button
                variant="primary"
                href="https://kittengram-9e684.firebaseapp.com/"
              >
                Here
              </Button>
            </Card.Body>
          </Card>
          <Card style={{ width: "32rem" }}>
            <Card.Img variant="top" src={terygram} />
            <Card.Body>
              <Card.Title>Terygram</Card.Title>
              <Card.Text>
                A chat messaging app made with Flutter and Cloud Firestore.
              </Card.Text>
              <Button
                variant="primary"
                href="https://github.com/perrylzx/terygram/"
              >
                Here
              </Button>
            </Card.Body>
          </Card>
          <Card style={{ width: "32rem" }}>
            <Card.Img variant="top" src={todolist} />
            <Card.Body>
              <Card.Title>Todolist</Card.Title>
              <Card.Text>A todolist made in react and redux.</Card.Text>
              <Button
                variant="primary"
                href="http://perrylzx.github.io/to-do-list"
              >
                Here
              </Button>
            </Card.Body>
          </Card>
          <Card style={{ width: "32rem" }}>
            <Card.Img variant="top" src={goggle} />
            <Card.Body>
              <Card.Title>Clone of Google Search</Card.Title>
              <Card.Text>
                A clone of the google search page for my first project.
              </Card.Text>
              <Button
                variant="primary"
                href="http://perrylzx.github.io/google-clone"
              >
                Here
              </Button>
            </Card.Body>
          </Card>
        </div>
      </div>
    );
  }
}
